<template>
  <div class="ml-lg-2 mr-4">
    <v-card
      v-if="isVisible"
      width="100%"
      :max-width="$vuetify.breakpoint.mobile ? '100%' : 330"
      class="contacts-list overflow-y-auto mb-lg-6"
    >
      <v-toolbar
        class="toolbar"
        flat
      >
        <v-autocomplete
          :search-input.sync="filter"
          :items="autocompleteItems"
          class="font-weight-medium body-2 text-label"
          outlined
          dense
          hide-details
          clearable
          label="Search for contact..."
          append-icon="mdi-magnify"
        />
      </v-toolbar>

      <v-list flat>
        <v-list-item-group mandatory>
          <v-list-item class="list-item">
            <v-list-item-avatar>
              <v-checkbox
                class="pl-2"
                @click="onVisibleAllCheckboxValState = !onVisibleAllCheckboxValState"
                :value="onVisibleAllCheckboxValState"
              />
            </v-list-item-avatar>

            <v-list-item-title>
              Select all contacts
            </v-list-item-title>
          </v-list-item>

          <v-divider
            class="mx-5"
          />

          <template v-for="({ name, id }, index) in filteredContactsList">
            <v-list-item
              class="list-item"
              :key="`contact-${id}`"
              :to="{ name: $constants.ROUTE_NAMES.CONTACT, params: { id: id } }"
            >
              <v-list-item-avatar
                class="avatar-b"
              >
                <letter-avatar
                  :name="name"
                  class="avatar-img"
                  v-if="!contactsListDelete.length"
                />

                <v-checkbox
                  v-model="contactsListDeleteCheckbox"
                  :value="id"
                  class="avatar-checkbox d-none pl-2"
                  :class="{'d-block': contactsListDelete.length}"
                />
              </v-list-item-avatar>

              <v-list-item-title class="text-left body-2 px-0 label--text font-weight-medium">
                {{ name }}
              </v-list-item-title>
            </v-list-item>
            <v-divider
              v-if="index !== contactsList.length - 1"
              :key="index"
              class="mx-5"
            />
          </template>
        </v-list-item-group>
      </v-list>
    </v-card>

    <Modal
      :is-close-button-exist="false"
      :modal-opened="modalOpened"
      @modal:close="modalOpened = false"
    >
      <h1 class="text-center fs-28 my-4">
        Are you sure?
      </h1>

      <div class="d-flex mb-4">
        <v-btn
          :min-width="137"
          width="auto"
          height="50"
          depressed
          class="flex-grow-1 primary--text btn-background-grey font-weight-bold"
          @click="modalOpened = false"
        >
          Cancel
        </v-btn>

        <v-btn
          :min-width="137"
          width="auto"
          height="50"
          :loading="deleting"
          depressed
          class="flex-grow-1  ml-5 color-red white--text font-weight-bold"
          @click="handleMultipleDelete"
        >
          Delete
        </v-btn>
      </div>
    </Modal>

    <v-bottom-sheet
      v-model="bottomSheetVisible"
      inset
      hide-overlay
      persistent
      origin="bottom center"
      max-width="600px"
    >
      <v-card class="d-flex align-center justify-center flex-wrap px-2 py-3 mb-4">
        <span class="mr-6">
          {{ contactsListDelete.length }} {{ contactsListDelete.length > 1 ? 'contacts' : 'contact' }} selected
        </span>
        <div>
          <v-btn
            class="mx-1 error--text btn-background-grey"
            min-width="160"
            height="50"
            depressed
            @click="modalOpened = true"
          >
            Delete
          </v-btn>

          <v-btn
            class="mx-1 primary--text btn-background-grey"
            width="160"
            height="50"
            depressed
            @click="clearContactsListDelete"
          >
            Clear selection
          </v-btn>
        </div>
      </v-card>
    </v-bottom-sheet>
  </div>
</template>

<script>
import LetterAvatar from '@components/LetterAvatar';
import Modal from '@components/Modal/Modal.vue';

import { createNamespacedHelpers } from 'vuex';
import {
  mutations as contactsMutations,
  getters as contactsGetters,
  actions as contactsActions,
} from '@modules/contacts/constants';

const {
  mapMutations: mapContactsMutations,
  mapGetters: mapContactsGetters,
  mapActions: mapContactsActions,
} = createNamespacedHelpers('contacts');

export default {
  name: 'ContactsList',
  components: {
    LetterAvatar,
    Modal,
  },
  props: {
    contactsList: {
      type: Array,
      required: true,
    },
    isVisible: {
      type: Boolean,
      default: true,
    },
  },
  watch: {
    contactsListDelete(val) {
      this.updateContactsListDelete(val);

      if (!this.contactsListDelete.length) {
        this.bottomSheetVisible = false;
        this.onVisibleAllCheckboxValState = false;
      } else {
        this.bottomSheetVisible = true;
      }
    },
    onVisibleAllCheckboxValState(value) {
      if (value) {
        const selected = [];

        if (value) {
          this.contactsList.forEach((contact) => {
            selected.push(contact.id);
          });
        }

        this.updateContactsListDelete(selected);
      } else {
        this.clearContactsListDelete();
      }
    },
  },
  data() {
    return {
      modalOpened: false,
      bottomSheetVisible: false,
      onVisibleAllCheckboxValState: false,
      filter: '',
      deleting: false,
    };
  },
  methods: {
    ...mapContactsActions({
      deleteContacts: contactsActions.DELETE_CONTACTS,
      getContacts: contactsActions.GET_CONTACTS,
    }),
    ...mapContactsMutations({
      updateContactsListDelete: contactsMutations.UPDATE_CONTACTS_LIST_DELETE,
      clearContactsListDelete: contactsMutations.CLEAR_CONTACTS_LIST_DELETE,
    }),
    handleMultipleDelete() {
      this.deleting = true;
      this.deleteContacts({ ids: this.contactsListDelete })
        .then(() => {
          this.$notificationSuccess('The contacts was deleted');
          this.getContacts()
            .then((data) => {
              this.clearContactsListDelete();
              if (window.innerWidth >= 1264) {
                this.$router.push({ name: this.$constants.ROUTE_NAMES.CONTACT, params: { id: data[0].id } }).catch(() => {});
              } else {
                this.$router.push({ name: this.$constants.ROUTE_NAMES.MOBILE_CONTACT, params: { id: data[0].id } }).catch(() => {});
              }
            });
        })
        .catch(() => this.$notificationError('The contacts wasn\'t deleted'))
        .finally(() => {
          this.deleting = false;
          this.modalOpened = false;
        });
    },
  },
  computed: {
    autocompleteItems() {
      return this.contactsList.map(({ name }) => name);
    },
    filteredContactsList() {
      if (!this.filter) {
        return this.contactsList;
      }
      return this.contactsList.filter(({ name }) => name.toLowerCase().includes(this.filter.toLowerCase()));
    },
    contactsListDeleteCheckbox: {
      get() {
        return this.contactsListDelete;
      },
      set(id) {
        this.updateContactsListDelete(id);
      },
    },
    ...mapContactsGetters({
      contactsListDelete: contactsGetters.CONTACTS_LIST_DELETE,
    }),
  },
  beforeDestroy() {
    this.clearContactsListDelete();
  },
};
</script>

<style lang="scss" scoped>
.contacts-list {
  max-height: calc(100vh - 170px);
  height: fit-content;
}

.v-list-item--active:not(:first-child) {
  background-color: $list-active-background;
  color: $list-active-background;

  .v-list-item__title {
    color: $list-active-text !important;
  }
}

.toolbar {
  position: sticky;
  top: 0;
  z-index: 1;
}

::v-deep .v-bottom-sheet.v-dialog {
  box-shadow: none;
  border-top-right-radius: $app-border-radius;
  border-top-left-radius: $app-border-radius;
}

.avatar-b:hover .avatar-img {
  display: none !important;
}

.avatar-b:hover .avatar-checkbox {
  display: block !important;
}
</style>
