<template>
  <contacts-page-wrapper>
    <template v-if="contacts.data.length">
      <contacts-list
        v-if="contacts.data.length"
        :is-visible="isVisibleList"
        :contacts-list="contacts.data"
      />
      <router-view />
    </template>
    <template v-else-if="!contacts.loading && !contacts.data.length">
      <base-no-list-message>
        There are no contacts yet.
      </base-no-list-message>
    </template>
  </contacts-page-wrapper>
</template>

<script>
import BaseNoListMessage from '@components/BaseNoListMessage';
import ContactsPageWrapper from '@modules/contacts/components/ContactsPageWrapper/ContactsPageWrapper.vue';
import ContactsList from '@modules/contacts/components/ContactsList/ContactsList.vue';

import { createNamespacedHelpers } from 'vuex';
import { identity } from 'ramda';
import { actions as contactsActions } from '@modules/contacts/constants';

const {
  mapActions: mapContactsActions,
  mapState: mapContactsState,
} = createNamespacedHelpers('contacts');

export default {
  name: 'Contacts',
  components: {
    BaseNoListMessage,
    ContactsPageWrapper,
    ContactsList,
  },
  watch: {
    contacts(value, prevValue) {
      console.log('lllll');
      if (prevValue.data.length === 0 && value.data.length > 0) {
        if (!this.$route.params.id) {
          this.$router.push({ name: this.$constants.ROUTE_NAMES.CONTACT, params: { id: value.data[0].id } });
        }
      } else if (prevValue.data.length > 0 && value.data.length > 0) {
        if (!this.$route.params.id) {
          this.$router.push({ name: this.$constants.ROUTE_NAMES.CONTACT, params: { id: value.data[0].id } });
        }
      }
    },
  },
  computed: {
    isVisibleList() {
      return (
        this.$route.name === this.$constants.ROUTE_NAMES.CONTACTS
        || (this.$route.name === this.$constants.ROUTE_NAMES.CONTACT)
      );
    },
    ...mapContactsState(['contacts']),
  },
  methods: {
    isMobile() {
      if (!this.contacts.data.length) return;

      if (window.innerWidth < 1264 && !this.$route.path.includes('mobile')) {
        const path = `/mobile${this.$route.path}`;

        this.$router.push(path);
      } else {
        let path = `/contacts/${this.contacts.data[0].id}`;

        if (parseInt(this.$route.path.slice(-1), 10)) {
          path = this.$route.path;
        }

        if (this.$route.path !== path) this.$router.push(path);
      }
    },
    ...mapContactsActions({
      getContacts: contactsActions.GET_CONTACTS,
    }),
  },
  mounted() {
    this.getContacts();

    window.addEventListener('resize', this.isMobile, { passive: true });
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.isMobile);
  },
  beforeUpdate() {
    if (this.$route.matched.length === 1) {
      let path = {
        name: this.$constants.ROUTE_NAMES.CONTACT,
        params: { id: this.contacts?.data[0]?.id },
      };
      setTimeout(() => {
        this.$nextTick(() => {
          if (!this.contacts?.data[0]?.id) {
            path = {
              name: this.$constants.ROUTE_NAMES.CONTACTS,
            };
          }
          this.$router.push(path).catch(identity);
        });
      });
    }
  },
  beforeRouteEnter(_, to, next) {
    if (window.innerWidth < 1264) {
      next({ name: 'MobileContacts', params: { id: to.params.id } });
    }

    next();
  },
};
</script>
