<template>
  <base-layout :is-header-showed="!isHeaderHidden">
    <template #header>
      <div class="d-flex justify-space-between align-center full-width">
        <div class="d-flex">
          <v-btn
            v-if="isBackButtonVisible"
            text
            depressed
            rounded
            color="transparent"
            min-width="auto"
            height="auto"
            class="px-0"
            :to="{ name: $constants.ROUTE_NAMES.CONTACT }"
          >
            <v-img
              class="mr-4"
              alt="Back"
              :max-width="18"
              :src="require('@images/icons/drivers/back.svg')"
            />
          </v-btn>

          <h1
            class="font-weight-bold primary-dark--text"
          >
            {{ $route.meta.pageTitle }}
          </h1>
        </div>

        <v-btn
          :to="{ name: $constants.ROUTE_NAMES.CONTACT_CREATE }"
          v-if="isHeaderControlsVisible"
          default
          depressed
          class="flex-shrink-1 flex-grow-0 primary-light primary--text font-weight-medium custom__btn"
          color="success"
          max-width="auto"
          :min-width="50"
          :height="35"
        >
          <span>
            Add contact
          </span>

          <v-img
            alt="add"
            :max-width="18"
            class="ml-3"
            :src="require('@images/icons/contacts/add-user.svg')"
          />
        </v-btn>
      </div>
    </template>

    <template #content>
      <div class="content-page-wrapper d-flex flex-column flex-md-row flex-shrink-1 px-lg-6 py-lg-8 px-0 my-md-0 mx-lg-n2 mx-md-auto">
        <slot></slot>
      </div>
    </template>
  </base-layout>
</template>

<script>
import BaseLayout from '@components/BaseLayout';

export default {
  name: 'ContactsPageWrapper',
  components: { BaseLayout },
  computed: {
    isBackButtonVisible() {
      return this.$route.name === this.$constants.ROUTE_NAMES.CONTACT_EDIT && !this.$vuetify.breakpoint.mobile;
    },
    isHeaderHidden() {
      return (
        (this.$route.name === this.$constants.ROUTE_NAMES.CONTACT && this.$vuetify.breakpoint.mobile)
        || (this.$route.name === this.$constants.ROUTE_NAMES.CONTACT_EDIT && this.$vuetify.breakpoint.mobile)
      );
    },
    isHeaderControlsVisible() {
      return (
        (this.$route.name === this.$constants.ROUTE_NAMES.CONTACTS)
        || (this.$route.name === this.$constants.ROUTE_NAMES.CONTACT && !this.$vuetify.breakpoint.mobile)
      );
    },
  },
};
</script>

